import BioSingin from './BioSingin/BioSignin'
import BioLogin from './BioLogin/BioLogin';
import React, { useEffect, useRef } from 'react';
import { useContext } from 'react';
import Context from '../../Context/Context';
import MobileLogin from './BioLogin/MobileLogin';
import MobileSignin from './BioSingin/MobileSignin';
import { useDispatch } from 'react-redux';
import './register.css'
import generateID from '../../hooks/userID';
import setUsers from '../../hooks/setUsers';


function BioRegistratee(){
    let dispatch = useDispatch()
    let name = useRef()
    let login = useRef()
    let password = useRef()
    let img
    let timer1, timer2, timer3
    let confE
    let confP 
    let confN 
    let conf = {'name': false, 'login': false, 'password': false, 'img':false}
    let activeImg
    const {screenWidth, register, usersDate} = useContext(Context)
    ///////////////////////////////////////////
    function clearAll(){
        dispatch({type: 'REMOVE_C_U'})
        dispatch({type: 'REMOVE_C_P'})
        dispatch({type: 'REMOVE_TR'})
        dispatch({type: 'REMOVE_IR'})
        dispatch({type: 'REMOVE_RG'})
    }
    //////////////////////////////////////////////////////////////////////
    function setRegister(arg){
        dispatch({type: 'SET_RG', item: arg})
    }
/////////////////////////////////////////////////////////////////

function missedE(){
    login.current.style.borderBottom = '3px solid red'
    login.current.value = ''
    timer1 = setTimeout(()=>{
        if(login.current) login.current.style.borderBottom = '0.5px solid rgba(0, 0, 0, 0.24)'
        clearTimeout(timer1)
    }, 2000)
    
}
function missedP(){
    password.current.style.borderBottom = '3px solid red'
    password.current.value = ''
    timer2 = setTimeout(()=>{
        if(password.current) password.current.style.borderBottom = '0.5px solid rgba(0, 0, 0, 0.24)'
        clearTimeout(timer2)
    }, 2000)
    
}

function missedN(){
    name.current.style.borderBottom = '3px solid red'
    name.current.value = ''
    timer3 = setTimeout(()=>{
        if(name.current) name.current.style.borderBottom = '0.5px solid rgba(0, 0, 0, 0.24)'
        clearTimeout(timer3)
    }, 2000)
    
}
//////////////////////////////////////////

function callMistake(arr1, arr2, arr3){
    console.log(usersDate.length);
    if(!arr1.length) missedE(login)
    if(!arr2.length) missedP(password)
    if(!arr3.length) missedN(name)
}
///////////////////////////////

function configurate(){
    console.log(name.current.value);
    console.log(login.current.value);
    console.log(password.current.value);
    console.log(img);
    let isName = name.current.value.length >= 3? true: false;
    let isLogin = login.current.value.length >= 3? true: false;
    let isPassword = password.current.value.length >= 3? true: false;
    let isImg = img?.length >= 1? true: false;
    console.log(isName);
    if(isName && isLogin && isPassword && isImg) return true
    return false
}
//////////////////////////

function setImg(e){
    activeImg ? activeImg.classList.remove('choosed_img') : activeImg = e;
    e.classList.add('choosed_img')
    img = `${e.id}`
    activeImg = e
}
/////////////////////////////////

async function Registrate(){
    console.log(configurate());
    if(configurate()){
    let newUser = await {
        email: login.current.value,
        password: password.current.value,
        name: name.current.value,
        img: img,
        id: generateID(),
        friends: [],
        treads: {}
    }
    await dispatch({type: 'SET_UD', item: [...usersDate, newUser]})
    await setUsers([...usersDate, newUser])
    await setRegister(false)
    }else{
   
        if(!conf.login) confE = false
        if(!conf.password) confP = false
        if(!conf.name) confN = false

        callMistake(confE, confP, confN)
    }
} 
//////////////////////////////////////////////
    useEffect(()=>{
        clearAll()
    }, [])
///////////////////////////////////////////////////////
    if(screenWidth > 500){
        return(
            <Context.Provider value={{setImg, Registrate, usersDate, name, login, password, img}}>
        <div className="register_bio">
            {register ? <BioSingin  setRegister={setRegister}/>:<BioLogin setRegister={setRegister}/>}
            
        </div>
        </Context.Provider>
        )
    }
    else {
        return (
            <Context.Provider value={{setImg, Registrate, usersDate, name, login, password, img}}>
    <div className="mobile_bio">
        {register ? <MobileSignin setRegister={setRegister}/>:<MobileLogin setRegister={setRegister}/>}
        </div>
        </Context.Provider>
    )
    }
        
}
export default BioRegistratee