function CheckIsAccaunt(login, name, usersDate, Registrate, setRegister){
    let mass = []
    console.log(login);
    console.log(usersDate[0]);
    if(usersDate[0]){
        usersDate.forEach(item=>{
            mass.push(item.email === login.current.value && item.name === name.current.value)
        })
    }
    
    if(!mass.includes(true)){
        Registrate()
        console.log('something');
    }
    else{
        setRegister(false)
        console.log('anything');
    }
    
}
export default CheckIsAccaunt